<template>
    <div id="productCategroys">
        <!--  -->
        <el-breadcrumb separator="/" style="padding-left:10px;padding-bottom:10px;font-size:12px;">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card class="box-card">
            <el-container style="margin-bottom:20px;">
                <el-alert
                        show-icon
                        title=":  "
                        type="warning">
                </el-alert>
            </el-container>
            <el-row :gutter="6">
                <el-col :span="5">
                    <el-cascader
                            size="small"
                            placeholder=""
                            :change-on-select="true"
                            @change="selectChange"
                            v-model="categorykeys"
                            :props="searchSelectProps"
                            :options="cateories"
                            clearable
                    ></el-cascader>
                </el-col>
                <el-col :span="6">
                    <el-input
                            clearable
                            size="small"
                            v-model="queryMap.name"
                            placeholder=""
                            @clear="search"
                            class="input-with-select"
                    ></el-input>
                </el-col>
                <el-col :span="5">
                    <template>
                        <el-select size="small" v-model="queryMap.status" @click="search" placeholder="">
                            <el-option label="" :value="0"></el-option>
                            <el-option label="" :value="1"></el-option>
                            <el-option label="" :value="2"></el-option>
                        </el-select>
                    </template>
                </el-col>
                <el-col :span="8">
                    <el-button size="small" type="primary" icon="el-icon-search" @click="search"></el-button>
                    <el-button size="small" icon="el-icon-refresh-right" type="warning" @click="resetForm"></el-button>
                    <el-button size="small" type="success" icon="el-icon-circle-plus-outline" @click="openAdd"
                               v-hasPermission="'product:add'">
                    </el-button>
                    <el-button size="small" icon="el-icon-refresh" @click="getproductList"></el-button>
                </el-col>
            </el-row>

            <!--  -->
            <template>
                <el-table
                        size="mini"
                        v-loading="loading"
                        border
                        stripe
                        :data="productData"
                        style="width: 100%;margin-top:20px;"
                        height="400"
                >
                    <el-table-column prop="id" type="index" label="ID"></el-table-column>

                    <el-table-column prop="imageUrl" label="" align="center" width="150px" padding="0px">
                        <!--            <template slot-scope="scope">-->
                        <!--              <img-->
                        <!--                slot="error"-->
                        <!--                :src="'https://www.zykhome.club/'+scope.row.imageUrl"-->
                        <!--                alt-->
                        <!--                style="width: 55px;height:55px"-->
                        <!--              />-->
                        <!--            </template>-->
                        <template slot-scope="scope">
                            <el-popover placement="right" trigger="hover">
                                <img :src="'https://www.zykhome.club/'+scope.row.imageUrl"
                                     style="height: 200px;width: 200px"/>
                                <img slot="reference" :src="'https://www.zykhome.club/'+scope.row.imageUrl"
                                     :alt="scope.row.imgUrl" style="height: 38px;width: 38px;cursor: pointer">
                            </el-popover>
                        </template>
                    </el-table-column>

                    <el-table-column prop="name" label=""></el-table-column>


                    <el-table-column prop="pnum" :show-overflow-tooltip='true' label=""></el-table-column>
                    <el-table-column label="" width="100">
                        <template slot-scope="scope">
                            <el-tag type="success" size="mini" closable v-text="scope.row.model"></el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column prop="unit" label="" width="80"></el-table-column>
                    <el-table-column prop="remark" label=""></el-table-column>
                    <el-table-column prop="status" label="" width="100">
                        <template slot-scope="scope">
                            <el-tag size="mini" type="danger" effect="plain" v-if="scope.row.status==1"></el-tag>
                            <el-tag size="mini" effect="plain" v-if="scope.row.status==0"></el-tag>
                            <el-tag size="mini" effect="plain" type="warning" v-if="scope.row.status==2"></el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column prop="createTime" label=""></el-table-column>
                    <el-table-column label="">
                        <template slot-scope="scope">
                            <!--              -->

                            <span v-if="scope.row.status==1">
                 <el-popconfirm

                         @onConfirm="back(scope.row.id)"
                         style="margin-left:10px;"
                         confirmButtonText=''
                         cancelButtonText=''
                         icon="el-icon-info"
                         iconColor="green"
                         title=""
                 >
                <el-button type="text" size="mini" slot="reference" icon="el-icon-back"></el-button>
                 </el-popconfirm>
              <el-button style="margin-left:10px;" type="text" size="mini" icon="el-icon-delete"
                         @click="del(scope.row.id)"></el-button>
              </span>

                            <!--              -->
                            <span v-if="scope.row.status==0">
               <el-button type="text" size="mini" icon="el-icon-edit" @click="edit(scope.row.id)"></el-button>
              <el-popconfirm
                      @onConfirm="remove(scope.row.id)"
                      style="margin-left:10px;"
                      confirmButtonText=''
                      cancelButtonText=''
                      icon="el-icon-info"
                      iconColor="red"
                      title=""
              >
              <el-button type="text" slot="reference" size="mini" icon="el-icon-s-operation"></el-button>
            </el-popconfirm>
              </span>

                            <!--          -->
                            <span v-if="scope.row.status==2">
              <el-popconfirm
                      @onConfirm="publish(scope.row.id)"
                      style="margin-left:10px;"
                      confirmButtonText=''
                      cancelButtonText=''
                      icon="el-icon-info"
                      iconColor="blue"
                      title=""
              >
              <el-button type="text" slot="reference" size="mini" icon="el-icon-s-operation"></el-button>
            </el-popconfirm>
              <el-button style="margin-left:10px;" type="text" size="mini" icon="el-icon-delete"
                         @click="del(scope.row.id)"></el-button>
                </span>
                        </template>
                    </el-table-column>
                </el-table>
            </template>
            <!--  -->
            <el-pagination
                    style="margin-top:10px;"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="this.queryMap.pageNum"
                    :page-sizes="[6, 10, 15, 20]"
                    :page-size="this.queryMap.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
            ></el-pagination>
            <!--  -->
            <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="closeAddDialog">
        <span>
          <el-form
                  size="mini"
                  :model="addRuleForm"
                  :rules="addRules"
                  ref="addRuleFormRef"
                  label-width="100px"
                  class="demo-ruleForm"
          >
            <el-form-item label="" prop="name">
              <el-input v-model="addRuleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <!--  -->
              <el-upload
                      :action="uploadApi"
                      :class="{ disabled: uploadDisabled }"
                      list-type="picture-card"
                      :limit="limitCount"
                      :on-change="handleChange"
                      :on-remove="handleRemove"
                      accept="image/*"
                      :on-success="handleSuccess"
                      :headers="headerObject"
                      ref="upload"
                      :on-preview="handlePictureCardPreview"
              >
                <i class="el-icon-plus"></i>
              </el-upload>
            </el-form-item>
            <el-row>
              <el-col :span="12">
                <div class="grid-content bg-purple-light">
                  <el-form-item label="" prop="model">
                    <el-input v-model="addRuleForm.model"></el-input>
                  </el-form-item>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="categoryKeys">
                    <el-cascader
                            :options="cateories"
                            clearable
                            filterable
                            :props="selectProps"
                            v-model="addRuleForm.categoryKeys"
                    ></el-cascader>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="unit">
                    <el-input v-model="addRuleForm.unit"></el-input>
                  </el-form-item>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="grid-content bg-purple-light">
                  <el-form-item label="" prop="sort">
                    <el-input-number v-model="addRuleForm.sort" :min="1" :max="10" label=""></el-input-number>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>

            <el-form-item label="" prop="remark">
              <el-input type="textarea" v-model="addRuleForm.remark"></el-input>
            </el-form-item>
          </el-form>
        </span>
                <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="add" :disabled="btnDisabled" :loading="btnLoading"> </el-button>
        </span>
            </el-dialog>

            <!--  -->
            <el-dialog
                    title=""
                    :visible.sync="editDialogVisible"
                    width="50%"
                    @close="closeEditDialog"
            >
        <span>
          <el-form
                  size="mini"
                  :model="editRuleForm"
                  :rules="addRules"
                  ref="editRuleFormRef"
                  label-width="100px"
                  class="demo-ruleForm"
          >
            <el-form-item label="" prop="name">
              <el-input v-model="editRuleForm.name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="name">
              <!--  -->
              <el-upload
                      :action="uploadApi"
                      :class="{ disabled: uploadDisabled }"
                      list-type="picture-card"
                      :limit="limitCount"
                      accept="image/*"
                      :on-success="editHandleSuccess"
                      :headers="headerObject"
                      ref="upload2"
                      :on-preview="handlePictureCardPreview"
                      :file-list="imgFilesList"
              >
                <i class="el-icon-plus"></i>
              </el-upload>
            </el-form-item>
            <el-row>
              <el-col :span="12">
                <el-form-item label="" prop="model">
                  <el-input v-model="editRuleForm.model"></el-input>
                </el-form-item>
              </el-col>

              <el-col :span="12">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="categoryKeys">
                    <el-cascader
                            :options="cateories"
                            clearable
                            filterable
                            :props="selectProps"
                            v-model="editRuleForm.categoryKeys"
                    ></el-cascader>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <div class="grid-content bg-purple">
                  <el-form-item label="" prop="unit">
                    <el-input v-model="editRuleForm.unit"></el-input>
                  </el-form-item>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="grid-content bg-purple-light">
                  <el-form-item label="" prop="sort">
                    <el-input-number v-model="editRuleForm.sort" :min="1" :max="10" label=""></el-input-number>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>
          </el-form>
        </span>
                <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button
                  type="primary"
                  @click="update"
                  :disabled="btnDisabled"
                  :loading="btnLoading"
          > </el-button>
        </span>
            </el-dialog>
            <!--  -->
            <el-dialog :visible.sync="dialogVisible">
                <img width="100%" :src="dialogImageUrl" alt/>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                uploadApi:this.BASE_API_URL+'system/upload/image',
                btnLoading: false,
                btnDisabled: false,
                loading: true,
                headerObject: {
                    Authorization: LocalStorage.get(LOCAL_KEY_XINGUAN_ACCESS_TOKEN)
                }, //
                cateories: [], //
                selectProps: {
                    expandTrigger: "hover",
                    value: "id",
                    label: "name",
                    children: "children",
                    checkStrictly: false
                }, //
                searchSelectProps: {
                    expandTrigger: "hover",
                    value: "id",
                    label: "name",
                    children: "children",
                    checkStrictly: true
                }, //


                editDialogVisible: false,
                addDialogVisible: false, //
                total: 0, //
                productData: [], //
                queryMap: {
                    pageNum: 1,
                    pageSize: 6,
                    name: "",
                    categoryId: "",
                    supplier: "",
                    status: 0,

                }, //
                addRuleForm: {}, //
                editRuleForm: {}, //
                uploadDisabled: false,
                limitCount: 1,
                dialogImageUrl: "",
                dialogVisible: false,
                addRules: {
                    name: [
                        {required: true, message: "", trigger: "blur"},
                        {min: 2, max: 10, message: " 2  10 ", trigger: "blur"}
                    ],
                    unit: [
                        {required: true, message: "", trigger: "blur"},
                        {min: 1, max: 10, message: " 1  10 ", trigger: "blur"}
                    ],
                    model: [
                        {required: true, message: "", trigger: "blur"},
                        {min: 2, max: 10, message: " 2  10 ", trigger: "blur"}
                    ],
                    remark: [
                        {required: true, message: "", trigger: "blur"},
                        {min: 2, max: 10, message: " 2  10 ", trigger: "blur"}
                    ],
                    categorys: [
                        {required: true, message: "", trigger: "blur"}
                    ],
                    sort: [{required: true, message: "", trigger: "blur"}],
                    categoryKeys: [
                        {required: true, message: "", trigger: "blur"}
                    ]
                }, //
                imgFilesList: [],
                categorykeys: [] //
            };
        },
        methods: {
            //
            resetForm() {
                this.queryMap = {
                    pageNum: 1,
                    pageSize: 5,
                    name: "",
                    categoryId: "",
                    supplier: "",
                    status: 0,
                };
            },
            /**
             * 
             */
            openAdd() {
                this.getCatetorys();
                this.addDialogVisible = true;
            },
            /**
             * 
             */
            search() {
                this.queryMap.pageNum = 1;
                this.getproductList();
            },

            /**
             * 
             */
            async publish(id) {
                const {data: res} = await this.$http.put("business/product/publish/" + id);
                if (!res.success) {
                    return this.$message.error(":" + res.data.errorMsg);
                } else {
                    await this.getproductList();
                    return this.$message.success("");
                }
            },
            /**
             * 
             */
            async del(id) {
                var res = await this.$confirm(
                    ", ?",
                    "",
                    {
                        confirmButtonText: "",
                        cancelButtonText: "",
                        type: "warning"
                    }
                ).catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
                if (res === "confirm") {
                    const {data: res} = await this.$http.delete("business/product/delete/" + id);
                    if (res.success) {
                        this.$message.success("");
                        await this.getproductList();
                    } else {
                        this.$message.error(res.data.errorMsg);
                    }
                }
            },
            /**
             * 
             */
            async update() {
                this.$refs.editRuleFormRef.validate(async valid => {
                    if (!valid) {
                        return;
                    } else {
                        this.btnDisabled = true;
                        this.btnLoading = true;
                        const {data: res} = await this.$http.put(
                            "business/product/update/" + this.editRuleForm.id,
                            this.editRuleForm
                        );
                        if (res.success) {
                            this.$notify({
                                title: "",
                                message: "",
                                type: "success"
                            });
                            this.editRuleForm = {};
                            await this.getproductList();
                        } else {
                            this.$message.error(":" + res.data.errorMsg);
                        }
                        this.editDialogVisible = false;
                        this.btnDisabled = false;
                        this.btnLoading = false;
                    }
                });
            },
            /**
             * 
             */
            async edit(id) {
                const {data: res} = await this.$http.get("business/product/edit/" + id);
                if (res.success) {
                    this.editRuleForm = res.data;
                    const item = res.data;
                    this.imgFilesList.push({
                        url: "https://www.zykhome.club/" + item.imageUrl,
                        name: item.name,
                        id: item.id
                    });
                } else {
                    return this.$message.error("" + res.data.errorMsg);
                }
                const array = [];
                array.push(res.data.oneCategoryId);
                array.push(res.data.twoCategoryId);
                array.push(res.data.threeCategoryId);
                this.editRuleForm.categoryKeys = array;
                this.editDialogVisible = true;
            },
            /**
             * 
             */
            add() {

                this.$refs.addRuleFormRef.validate(async valid => {
                    if (!valid) {
                        return;
                    } else {
                        this.btnDisabled = true;
                        this.btnLoading = true;
                        const {data: res} = await this.$http.post(
                            "product/add",
                            this.addRuleForm
                        );
                        if (res.success) {
                            this.$message.success("");
                            this.addRuleForm = {};
                            await this.getproductList();
                        } else {
                            return this.$message.error(":" + res.data.errorMsg);
                        }
                        this.addDialogVisible = false;
                        this.btnDisabled = false;
                        this.btnLoading = false;
                    }
                });
            },
            /**
             * 
             */
            async remove(id) {
                const {data: res} = await this.$http.put("business/product/remove/" + id);
                if (!res.success) {
                    return this.$message.error(":" + res.data.errorMsg);
                } else {
                    await this.getproductList();
                    return this.$message.success("");
                }
            },
            /**
             * 
             */
            async back(id) {
                const {data: res} = await this.$http.put("product/back/" + id);
                if (!res.success) {
                    return this.$message.error(":" + res.data.errorMsg);
                } else {
                    await this.getproductList();
                    return this.$message.success("");
                }
            },
            /**
             * 
             */
            async getproductList() {
                const {data: res} = await this.$http.get("business/product/findProductList", {
                    params: this.queryMap
                });
                if (!res.success) {
                    return this.$message.error("");
                } else {
                    this.total = res.data.total;
                    this.productData = res.data.rows;
                }
            },
            /**
             * 
             */
            async getCatetorys() {
                const {data: res} = await this.$http.get(
                    "business/productCategory/categoryTree"
                );
                if (!res.success) {
                    return this.$message.error("");
                } else {
                    this.cateories = res.data.rows;
                }
            },
            handleChange(file, fileList) {
                this.uploadDisabled = fileList.length >= this.limitCount;
            },
            handleRemove(file, fileList) {
                this.uploadDisabled = fileList.length >= this.limitCount;
                this.addRuleForm.imageUrl = "";
            },
            //
            handleSizeChange(newSize) {
                this.queryMap.pageSize = newSize;
                this.getproductList();
            },
            //
            handleCurrentChange(current) {
                this.queryMap.pageNum = current;
                this.getproductList();
            },
            //
            closeAddDialog() {
                this.$refs.addRuleFormRef.clearValidate();
                this.addRuleForm = {};
                this.$refs.upload.clearFiles();
            },
            //
            closeEditDialog() {
                this.$refs.editRuleFormRef.clearValidate();
                this.editRuleForm = {};
                this.$refs.upload2.clearFiles();
                this.imgFilesList = [];
            },
            handleSuccess(response, file, fileList) {
                this.addRuleForm.imageUrl = response.msg;
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            },
            //
            editHandleSuccess(response, file, fileList) {
                this.editRuleForm.imageUrl = response.msg;
            },
            /**
             * 
             */
            selectChange() {
                var str = "";
                for (var i = 0; i < this.categorykeys.length; i++) {
                    str += this.categorykeys[i] + ",";
                }
                str = str.substring(0, str.length - 1);
                this.queryMap.categorys = str;
            }
        },
        created() {
            this.getproductList();
            this.getCatetorys();
            setTimeout(() => {
                this.loading = false;
            }, 500);
        }
    };
</script>


